import { MacroSearchSuggestion } from "../suggestion.types"
import { generateMacroIds } from "./generateMacroIds"

const makeSuggestionId = (
  key: string,
  type?: string,
  groups?: MacroSearchSuggestion["groups"],
): string => {
  if (type && groups) {
    const value = groups.map(group => ({ ...group, operation: "in" as const }))
    // This will fail when we can have multiple group suggestion
    return generateMacroIds({ type, value })[0]
  }
  return `${type}|${key}`
}

export { makeSuggestionId }
